import React, {Component} from 'react';

import './ContactSection1.css';

class ContactSection1 extends Component {
  render() {
    return (
     <section >
	   <div class="container">
        <div class="row">
        
          <div class="col-lg-12 col-md-12 text-center">
            <div class="mt-12 mx-auto">
              <h4 class="mb-3">Have questions about Atyx.io?</h4>
              <h4 class="mb-3">Want to learn more about our services?</h4>
              <h4 class="mb-3">Interested in retaining us for a project, or working together on a collaboration?</h4>
              <h4 class="mb-3">At Atyx, we welcome any and all inquiries.</h4>
              <p class="text-muted mb-0">Whether you want to hire us, learn more about our services, or just chat about our areas of expertise, were here for you. We can be reached by email using the form below, or at any of the contact options provided. </p>
            </div>
          </div>
       </div>
      </div>
    </section>
	
    );
  }
}

export default ContactSection1;


